import React from 'react';
import Grid from '@mui/material/Grid';
import './Preparation.css';

function Preparation(props) {
	return (
		<Grid
			container
			className=' recipeContainer__box recipeContainer__preparation'>
			<Grid item>
				<h3>Przygotowanie</h3>
				<ol onClick={props.crossLine}>
					<li>
					Warzywa umy i osczy z wody. 
					</li>
					<li>
					Przygotowa ciasto: mk przesypa na stolnic i wymiesza ze szczypt soli. Do wymieszanej mki wbi jajko, wla letni wod i wyrobi elastyczne ciasto. Wyrobione rozwakowa na grubo 0,5 mm i wyci kubkiem krki.
					</li>
					<li>
					Przygotowa wypenienie: pory oraz mit pokroi w cienkie plastry. Cebul obra ze skry i zeszkli na 3 ykach rozgrzanego oleju na na patelni. Do zeszklonej cebuli doda pokrojone pory oraz mit, po czym skadniki doprawi do smaku mielonym imbirem, pieprzem oraz sol, wymiesza i smay przez 1,5 minuty na rednim ogniu. 
					</li>
					<li>
					Potem do smaonych skadnikw doda pokruszony drobno ser biay, ponownie wymiesza i smay przez kolejne 1,5 minuty. Usmaone cign z ognia, lekko przestudzi i yeczk naoy na wycite krki, po czym cao zlepi rcznie. 
					</li>
					<li>Zlepione wrzuci do 1 l gotujcej si wody. Gotowa przez trzy minuty, potem odcedzi yk cedzakowa i wyoy na talerz.</li>
					<li>Makaron rurki zala 1 l lekko osolonej wody ugotowa do mikkoci i odcedzi. </li>
					<li>Rozgrza 3 yki oleju na patelni i zeszkli na nim obrany i pokrojony drobno czosnek. Do zeszklonego czosnku doda mielone miso wieprzowe i smay przez 1 minut na rednim ogniu.Potem doda do tego ugotowany wczeniej makaron i przecier pomidorowy. Wszystko doprawi do smaku pieprzem oraz sol, wymiesza i dusi pod przykryciem przez 4 minuty. </li>
					<li> Gotowe cign z ognia i wyoy na skadniki na talerzu. Cao zala jogurtem naturalnym i posypa mielonym tymiankiem.</li>
				</ol>
			</Grid>
		</Grid>
	);
}

export default Preparation;
